.cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 2px solid black;
    padding: 1rem 0;
    margin: 1rem 0;
}
.image{
    width: 6rem;
    height: 6rem;
}
.image img{
    width: 5rem;
    height: 5rem;
}
.cart-item div{
    margin-top: 10px;
}
.cart-item h3 {
    margin: 0 0 0.5rem 0;
    color: #363636;
}
.summary {
    width: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.price {
    font-weight: bold;
    color: black;
}
.quantity {
    font-weight: bold;
    border: 1px solid #ccc;
    padding: 0.25rem 0.75rem;
    border-radius: 6px;
    color: #363636;
}
.actions {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
}
.cart-item button {
    font: inherit;
    font-weight: bold;
    font-size: 1.25rem;
    color: black;
    border: 1px solid black;
    width: 3rem;
    text-align: center;
    border-radius: 6px;
    background-color: transparent;
    cursor: pointer;
    margin: 0.25rem;
  }
  
  .cart-item button:hover,
  .cart-item button:active {
    background-color: black;
    color: white;
  }
